//
.project-wrap {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 20px 0; }
//
.pw-thumbnail {
  max-width: 250px;
  a {
    cursor: pointer;
    display: flex;
    img {
      border-radius: 1em;
      padding: 4px;
      transition: all 0.2s ease;
      width: 100%;
      height: auto; } } }
//
.projects-thumbnail {
  width: 400px;
  height: 200px;
  a {
    img {
      border-radius: 1em;
      padding: 3px;
      width: 100%;
      height: 100%;
      object-fit: cover; } } }
.pw-thumbnail, .projects-thumbnail {
  a {
    img {
      transition: all 0.2s ease-in-out; }
    img:hover {
      opacity: 0.5; }
    img:hover {
      transform: scale(0.9); } } }

@media screen and (max-width: 500px) {
  .pw-thumbnail {
    max-width: 160px; } }

//
.sketchbook-container {
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  img {
    margin: 10px;
    width: 400px;
    height: auto; } }
