.banner-container {
  display: flex;
  flex-flow: column;
  padding: 3em 0;
  div {
    position: relative;
    width: 100%;
    margin-bottom: 4em;
    background-color: rgba($banner-color, 0.01);
    a {
      display: flex;
      height: inherit;
      justify-content: center;
      align-items: center;
      .img-container {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        img {
          max-width: 570px;
          max-height: 230px;
          object-fit: contain; } }
      p {
        position: absolute; } } }
  div:last-of-type {
    margin-bottom: 0; }
  div:hover {
    background-color: rgba($banner-color, 0.8); } }
